  max-width: 650px;
  z-index: 2;
  left: 0;
  bottom: 0;
}

.laptop-pic img {
  width: 100%;
  margin-bottom: -16%;
  margin-top: 16%;
}

@media screen and (min-width: 768px) {
  .laptop {
    padding-top: 230px;
  }

  .laptop h2 {
    margin: 22px 0px 65px 0px;
  }

  .laptop-pic {
    width: 70%;
  }
}

@media screen and (min-width: 992px) {
  .laptop-pic {
    position: absolute;
    width: 50%;
  }
}

@media screen and (min-width: 1081px) {
  .laptop {
    padding-top: 325px;
  }
}

@media screen and (min-width: 1200px) {
  .laptop .block-redlines {
    margin-bottom: 125px;
  }
}

/* Footer */

footer {
  padding: 117px 0 54px;
  color: #fff;
  font-family: Montserrat;
  background: linear-gradient(254.72deg,rgba(255,255,255,.276) -114.85%,rgba(255,255,255,0) 69.04%),url(./Images/D-flying.svg) no-repeat top center,url(./Images/D-footer.svg) no-repeat bottom right,#040613;
}

.footer-block {
  border-top: 1px solid #e5e5e5;
  padding-top: 35px;
  padding-bottom: 36px;
  margin-top: 52px;
  background-size: cover,165px,140px;
}

.connect-wrapper {
  max-width: 100%;
  width: 480px;
  margin: 0 auto 30px;
  text-align: justify;
}

.connect-title {
  margin-bottom: 20px;
  margin-top: initial;
  font-weight: bold;
  font-size: 22px;
  line-height: 1.215;
  text-align: center;
  min-width: 345px;
}

.connect-description {
  margin-bottom: 30px;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.55;
  color: rgba(256,256,256,.7);
}

.connect-contacts ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

.connect-contacts ul li {
  margin-bottom: 12px;
}

.connect-contacts ul li a {
  position: relative;
  padding-left: 40px;
  display: inline-block;
  text-decoration: none;
  font-weight: bold;
  font-size: 14px;
  color: #fff;
  line-height: 1.1;
}

.contacts-phone a:before {
  position: absolute;
  content: "";
  display: block;
  width: 18px;
  height: 18px;
  left: 3px;
  bottom: 4px;
  background: url(./Images/phone.svg) no-repeat center;
  background-size: contain;
  opacity: 80%;
}

.connect-contacts .contacts-email a {
  text-decoration: underline;
}

.connect-contacts .contacts-phone a {
  font-size: 24px;
}

.contacts-email a:before {
  position: absolute;
  content: "";
  display: block;
  width: 24px;
  height: 18px;
  left: 0;
  bottom: 0;
  background: url(./Images/mail.svg) no-repeat center;
  background-size: contain;
  opacity: 80%;
}


#socialnetwork-block-wrapper {
  position: static;
  padding: 0;
  margin-top: 71px;
}

#socialnetwork-block-wrapper:before {
  position: absolute;
  content: "";
  display: block;
  left: 0;
  right: 0;
  height: 1px;
  background: rgba(256,256,256,.3);
}

.socialnetwork-block {
  padding-top: 56px;
}

.socialnetwork-block-wrapper p {
  font-weight: 400;
  margin-bottom: 0;
}

.footer-links-wrapper {
  margin: 0;
  padding: 0;
  list-style: none;
}

.footer-links {
  margin-bottom: 15px;
}

.footer-pic {
  position: relative;
  display: inline-block;
  margin-right: 10px;
  border: 2px solid #f14d34;
  border-radius: 5px;
  color: #fff;
  width: 30px;
  height: 30px;
  background: #f14d34;
  text-align: center;
}

.facebook img {
  position: absolute;
  max-height: 17px;
  margin: 4px -4px;
}

.vkontakte img {
  position: absolute;
  margin: 7px -11px;
  max-height: 12px;
}

.telegram img {
  position: absolute;
  margin: 1px -13px;
  max-height: 25px;
}

.youtube img {
  position: absolute;
  margin: 2px -11px;
  max-height: 22px;
}

@media screen and (min-width: 601px) {
  .connect-wrapper {
    text-align: center;
  }

  .connect-title {
    font-size: 24px;
    text-align: unset;
  }
}

@media screen and (min-width: 768px) {
  .footer-block {
    margin-top: 45px;
    background-size: cover, 235px, 230px;
  }

  .connect-wrapper {
    text-align: unset;
    margin-right: 100px;
  }

  .connect-title {
    margin-top: initial;
    font-size: 32px;
  }

  .connect-contacts ul li a {
    font-size: 18px;
  }

  .connect-contacts .contacts-phone a {
    font-size: 32px;
  }
}

@media (min-width: 992px){
  .footer-block {
    background-size: auto;
  }

  .connect-title {
    font-size: 42px;
    min-width: auto;
  }

  .connect-description {
    margin-bottom: 140px;
  }
}

@media screen and (min-width: 1200px) {
  .footer-block .container {
    padding-left: 0;
  }
}

