/* Navigation */

.navbar-absolute {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  top: auto;
  background-color: #000;
  z-index: 1000;
}

.navbar {
  padding-left: 0;
  padding-right: 0;
  padding-top: 2px;
  padding-bottom: 6px;
  margin-left: -15px;
  margin-right: -15px;
  flex-wrap: wrap;
  z-index: 1000;
position: relative;
}

.collapse {
  border-bottom: solid 1px #fff;
  overflow-y: auto;
  max-height: calc(100vh - 49px);
  z-index: 1000;

}
Link:active {
  text-decoration: none;
}
.navbar-nav {
  flex-wrap: wrap;
}
.navbar li {
  border: none;
  transition: border-color 400ms;
  margin-right: 27px;
}

.navbar li:hover {
  border: none;
}

.navbar li.navigation-item a.nav-link {
  padding: 8px 20px;
  border-bottom: 1px solid #312a2a;
  line-height: 22px;
  font-size: 12px;
  font-style: normal;
  color: #fff;
}

nav .dropdown-menu {
  display: block;
  position: static;
  background-color: inherit;
  padding: 0;
  font-size: 12px;
  border-radius: 0;
  border: none;
  margin-top: 0;
}

nav .dropdown-item {
  color: #fff;
  transition: all 400ms;
  padding: 8px 20px 8px 35px;
  font-weight: 500;
  border-bottom: 1px solid #312a2a;
  line-height: 22px;
}

nav .dropdown-item:hover, nav .dropdown-item:focus {
  color: #f14d34;
  background-color: inherit;
}

.navbar a {
  text-decoration: none;
}

.navbar-toggler-icon {
  background: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(136,136,136,1.00)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3E%3C/svg%3E") no-repeat;
}

.navbar-toggler {
  margin-top: 1px;
}

.navbar li.navigation-item a.nav-link:hover,
.navbar li.navigation-item a.nav-link:focus {
  color: #f14d34;
  background-color: inherit;
}

.nav-icon {
  width: 160px;
  margin-right: 60px;
  margin-left: 15px;
}

.navbar-brand {
  margin-right: 0px;
}


@media screen and (min-width: 768px) {
  .navbar-absolute {
    position: absolute;
    bottom: auto;
    left: 0;
    right: 0;
    top: 30px;
    background-color: unset;
  }

  .nav-logo {
    width: 175px;
    margin-left: auto;
  }
  .navbar {
    padding-top: 8px;
    padding-bottom: 8px;
    margin-left: auto;
    margin-right: auto;
  }

  .navbar li.navigation-item a.nav-link {
    padding: 0;
    border-bottom: unset;
    line-height: unset;
  }

  .navbar li {
    border-top: 8px solid transparent;
    border-bottom: 3px solid transparent;
  }
  .navbar li:hover {
    border-top: 8px solid transparent;
    border-bottom: 3px solid #f14d34;
  }

  .collapse {
    order: 1;
    border-bottom: unset;
    max-height: unset;
    overflow-y: unset;
  }

  nav .dropdown-menu {
    max-height: 0;
    overflow: hidden;
    transition: all 0.5s;
    position: unset;
    background-color: #f14d34;
    padding: unset;
  }

  nav .dropdown-menu-2 {
    min-width: 120px;
  }

  nav .dropdown-item {
    padding: 3px 20px;
    border-bottom: unset;
    line-height: unset;
  }

  .dropdown-menu-2 .dropdown-item {
    width: 135px;
    padding-right: 0;
    padding-right: initial;
  }

  nav .dropdown-item:hover, nav .dropdown-item:focus {
    color: #fff;
    background-color: #d13018;
  }

  nav .navigation-item.dropdown:hover .dropdown-menu {
    max-height: 150px;
  }

  .navbar li.navigation-item a.nav-link:hover,
  .navbar li.navigation-item a.nav-link:focus {
    color: #fff;
  }
}

@media screen and (min-width: 992px) {
  nav .dropdown-menu-2 {
    min-width: 10rem;
  }

  .dropdown-menu-2 .dropdown-item {
    padding-right: 20px;
    width: 100%;
  }
}

header {
